import React from "react";
import "./style.css";
import NotificationsNoneIcon from '@mui/icons-material/NotificationsNone';
import NotificationsIcon from '@mui/icons-material/Notifications';
import { getNotifications } from "../services/api";
import ClearAllIcon from '@mui/icons-material/ClearAll';


class Notification extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      toggleNotification: false,
      listItems: [],
      isLoading: false,
      notificationList: []
    };
  }
  componentDidMount() {
    this.setState({ listItems: this.props.listItems });
    this.getNotifications();
  }
  componentDidUpdate(previousProps) {
    if (previousProps.listItems !== this.props.listItems) {
      this.setState({ listItems: this.props.listItems });
    }
  }

  getNotifications = async () => {
    this.setState ({isLoading: true});
    try{
      const notifications = await getNotifications('user');
      this.setState({
        notificationList: notifications
      })
    } catch (e) {
      console.log(e)
      this.setState({
        notificationList: []
      })
    } finally {
        this.setState({isLoading: false});
    }
  }

  toggleNotification = () => {
    const { toggleNotification } = this.state;
    this.setState({ toggleNotification: !toggleNotification });
  };

  clearAllMessages = async () => {
    console.log("clearing all messages....")

    try{
      let { notificationList } = this.state;
      notifications = await clearNotifications(notificationList);
      this.setState({
        notificationList: notifications
      })
    } catch (e) {
      console.log(e)
      this.setState({
        notificationList: []
      })
    } finally {
        this.setState({isLoading: false});
    }
  };

  render() {
    const { listItems, notificationList, isLoading } = this.state;
    let totalCount = 0;
    let notificationsIcon;
    let notificationListInfo;
    let clearAllIcon;
    if(!isLoading && notificationList && notificationList !== null){
      totalCount = notificationList.length;
      notificationListInfo = notificationList.map((item, index)=>
        <div className={"lineItmes"} key={`div${index}`}><h3 key={`h3${index}`}>{item.title}</h3>
        {item.content}<hr/></div>
      )

    }
    // listItems.map((i, k) => {
    //   totalCount = totalCount + i.list.length;
    // });
    // listItems.map((number, index) =>
 
    // number.list.forEach( (item, itemIndex) =>
    // listItemsInfo.push(
    //   <div key={`div${index}`}><h3 key={`h3${index}`}>{item.type}</h3>
    //   <li key={`li${index}`}>{item.content}</li></div>
    //   )
    // )
    // );
    if (totalCount > 0) {
      notificationsIcon = 
      <NotificationsIcon
        fontSize="inherit"
        style={{ fontSize: "75px", fill:"white"}}
        onClick={() => this.toggleNotification()}
    />;
    clearAllIcon = 
    <button onClick={() => this.clearAllMessages()}>
    <ClearAllIcon>
      fontSize="inherit"
      style={{ fontSize: "75px" }}
    </ClearAllIcon>
    </button>;
    } else {
      notificationsIcon =
      <NotificationsNoneIcon
        fontSize="inherit"
        style={{ fontSize: "75px", fill:"white"}}
        onClick={() => this.toggleNotification()}
    />;
    }
    return (
      <div className={"notification"} style={{ position: "relative" }}>
      <div className={"iconSection"}>
        {notificationsIcon}
        <span className={"iconBadge"}>{totalCount}</span>
      </div>
      {this.state.toggleNotification && (
        <div
                  style={{
                    position: "absolute",
                    width: "400px",
                    border: "0.5px solid #8080803d",
                    minHeight: "100px",
                    overflowY: "auto",
                    top: "70px"
                  }}
                  className={"notificationBar"}
                >
                  {clearAllIcon}
                  {notificationListInfo} 

        </div>
      )};
      </div>
    );
  }
}

export default Notification;